import { useLocation, useParams, useSearchParams } from "react-router-dom";

// import UserMemberTabsWrapper from "../../../Ui/UserMember/UserMemberTabsWrapper";
import RectangleTabWrapper from "../../../Ui/Tab/Rectangle/RectangleTabWrapper";
import RectangleTabCard from "../../../Ui/Tab/Rectangle/RectangleTabCard";

const InstituteDepartmentClassProfile = () => {
  const params = useParams();
  const getQuery = useLocation();
  const [searchParams] = useSearchParams();
  return (
    <>
      <RectangleTabWrapper
        customStyle={{
          borderTopLeftRadius: "6px",
          borderTopRightRadius: "6px",
        }}
      >
        {tabList.map((tab) => (
          <RectangleTabCard
            key={tab?.id}
            label={tab.label}
            isLink
            isActive={
              tab.id === "student" && !searchParams.get("n")
                ? true
                : searchParams.get("n") === tab.id
                ? true
                : false
            }
            parentState={getQuery.state}
            url={`/q/${params.username}/institute/member/academic/department/detail?type=cls-detail${tab.urlKey}`}
            isAuthorized
          />
        ))}
      </RectangleTabWrapper>
    </>
  );
};

export default InstituteDepartmentClassProfile;

const tabList = [
  {
    label: "students",
    id: "student",
    urlKey: "",
  },
  {
    label: "dse_student",
    id: "dse",
    urlKey: "&n=dse",
  },
  {
    label: "theory_classes",
    id: "theory",
    urlKey: "&n=theory",
  },
  {
    label: "practical_batches",
    id: "practical",
    urlKey: "&n=practical",
  },
];
