import { Link, useLocation, useParams } from "react-router-dom";
import { assestsAcademicDepartmentUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import style from "../../InstituteMember.module.css";
import { useTranslation } from "react-i18next";

import { useInstituteDepartmentDashboard } from "../../../Hooks/ApiHooks/AcademicDepartmentManager/academic-department-manager-api";
import { useState } from "react";
import InstituteAcademicDepartmentMenu from "./InstituteAcademicDepartmentMenu";

const InstituteAcademicDepartmentDetailSidebar = () => {
  const { t } = useTranslation();
  const params = useParams();
  const [open, setOpen] = useState(false);
  const getQuery = useLocation();
  const { instituteDepartmentDashboard, instituteDepartmentDashboardRefetch } =
    useInstituteDepartmentDashboard({
      data: {
        did: getQuery.state?.departmentId,
      },
      skip: !getQuery.state?.departmentId,
    });

  const onToggle = () => {
    setOpen((pre) => !pre);
  };
  return (
    <>
      <div className={style.iadds_continer}>
        <img
          src={`${assestsAcademicDepartmentUrl}/cover.svg`}
          alt="department cover icon"
          className={style.iads_cover}
        />
        <Link
          to={`/q/${params.username}/institute/member/academic/department/detail?a=site`}
          state={getQuery.state}
          style={{ textDecoration: "none" }}
        >
          <img
            className={style.iaddsc_globe}
            src={`${assestsAcademicDepartmentUrl}/globe.svg`}
            alt="edit icon"
          />
        </Link>
        <img
          className={style.iaddsc_edit}
          src={`${assestsAcademicDepartmentUrl}/edit.svg`}
          alt="edit icon"
          onClick={onToggle}
        />
      </div>

      <section
        className={style.iads_card}
        style={{
          marginTop: "-1.5rem",
          borderTopLeftRadius: "1rem",
          borderTopRightRadius: "1rem",
          backgroundColor: "#ffffff",
          position: "relative",
          padding: "0",
        }}
      >
        <section className={`${style.iads_card} ${style.iads_card_active}`}>
          <div className={style.iadsc_inner}>
            <h6>{`${instituteDepartmentDashboard?.dTitle}: ${
              instituteDepartmentDashboard?.dHead?.staffFirstName ?? ""
            } ${instituteDepartmentDashboard?.dHead?.staffMiddleName ?? ""} ${
              instituteDepartmentDashboard?.dHead?.staffLastName ?? ""
            }`}</h6>
            <p
              style={{
                color: "#121212",
                fontSize: "12px",
              }}
            >
              {instituteDepartmentDashboard?.active_academic_batch?.batchName ||
                t("not_active_batch")}
            </p>
          </div>
        </section>
      </section>

      <section className={style.iads_card}>
        <div className={style.iadsc_inner}>
          <h6>{t("classrooms")}</h6>
          <p>{t("class_in_batch")}</p>
        </div>
        <h6>
          {instituteDepartmentDashboard?.departmentSelectBatch?.classroom
            ?.length || 0}
        </h6>
      </section>
      <section className={style.iads_card}>
        <div className={style.iadsc_inner}>
          <h6>{t("total_staffs")}</h6>
          <p>{t("active_staff")}</p>
        </div>
        <h6>{instituteDepartmentDashboard?.staffCount || 0}</h6>
      </section>
      <section
        className={style.iads_card}
        style={{
          borderBottom: "0",
        }}
      >
        <div className={style.iadsc_inner}>
          <h6>{t("total_students")}</h6>
          <p>{t("student_studying")}</p>
        </div>
        <h6>
          {instituteDepartmentDashboard?.departmentSelectBatch?.ApproveStudent
            ?.length || 0}
        </h6>
      </section>

      {open && (
        <InstituteAcademicDepartmentMenu
          onClose={onToggle}
          menuData={instituteDepartmentDashboard}
          onRefetch={instituteDepartmentDashboardRefetch}
        />
      )}
    </>
  );
};

export default InstituteAcademicDepartmentDetailSidebar;
