import InstituteNavbarConfig from "../../Config/InstituteNavbarConfig";
import InstituteMemberConfig from "../../Config/InstituteMemberConfig";
import UserMemberContainerWrapper from "../../../Ui/UserMember/UserMemberContainerWrapper";
import GoBack from "../../../Shared/StaffStudentProfile/GoBack";
import InstituteDepartmentClassSidebar from "./InstituteDepartmentClassSidebar";
import style from "../../InstituteMember.module.css";
import InstituteDepartmentClassProfile from "./InstituteDepartmentClassProfile";
import InstituteDepartmentClass from "./InstituteDepartmentClass";
import { useLocation, useSearchParams } from "react-router-dom";
import InstituteTheoryClassDetail from "./InstituteTheoryClassDetail/InstituteTheoryClassDetail";
import InstitutePracticalBatchClassDetail from "./InstitutePracticalBatchClassDetail/InstitutePracticalBatchClassDetail";

const InstituteDepartmentClassConfig = () => {
  const [searchParams] = useSearchParams();
  const getQuery = useLocation();

  return (
    <InstituteNavbarConfig>
      <InstituteMemberConfig>
        <div className={style.iadc_side}>
          <UserMemberContainerWrapper
            customStyle={{
              minHeight: "fit-content",
              paddingBottom: "0",
            }}
          >
            <GoBack
              customStyle={{
                width: "100%",
                marginTop: "0",
              }}
            />
            <InstituteDepartmentClassSidebar />
          </UserMemberContainerWrapper>
        </div>
        <div className={style.iadc_main}>
          <UserMemberContainerWrapper
            customStyle={{
              paddingBottom: "0",
            }}
          >
            {searchParams.get("n") === "theory-detail" ? (
              <InstituteTheoryClassDetail
                clsId={getQuery.state?.clsId}
                instituteId={getQuery.state.instituteId}
              />
            ) : searchParams.get("n") === "practical-detail" ? (
              <InstitutePracticalBatchClassDetail
                clsId={getQuery.state?.clsId}
                instituteId={getQuery.state.instituteId}
              />
            ) : (
              <>
                <InstituteDepartmentClassProfile />
                <InstituteDepartmentClass />
              </>
            )}
          </UserMemberContainerWrapper>
        </div>
      </InstituteMemberConfig>
    </InstituteNavbarConfig>
  );
};

export default InstituteDepartmentClassConfig;
