import InstituteNavbarConfig from "../../Config/InstituteNavbarConfig";
import InstituteMemberConfig from "../../Config/InstituteMemberConfig";
import GoBack from "../../../Shared/StaffStudentProfile/GoBack";
import style from "../../InstituteMember.module.css";
import UserMemberContainerWrapper from "../../../Ui/UserMember/UserMemberContainerWrapper";
import InstituteAcademicDepartmentDetailSidebar from "./InstituteAcademicDepartmentDetailSidebar";
import InstituteAcademicDepartmentDetail from "./InstituteAcademicDepartmentDetail";
import { useLocation, useSearchParams } from "react-router-dom";
import { useInstituteDepartmentDashboard } from "../../../Hooks/ApiHooks/AcademicDepartmentManager/academic-department-manager-api";
import DepartmentSiteInfoConfig from "../../../UserStaffMember/DepartmentManager/DepartmentSiteInfo/DepartmentSiteInfoConfig";
import InstituteDepartmentClassConfig from "../InstituteDepartmentClass/InstituteDepartmentClassConfig";
const InstituteAcademicDepartmentDetailConfig = () => {
  const [searchParams] = useSearchParams();
  const getQuery = useLocation();
  const { instituteDepartmentDashboard } = useInstituteDepartmentDashboard({
    data: {
      did: getQuery.state?.departmentId,
    },
    skip: !getQuery.state?.departmentId,
  });
  // console.info("instituteDepartmentDashboard", instituteDepartmentDashboard);
  return (
    <>
      {searchParams.get("a") === "site" ? (
        <DepartmentSiteInfoConfig
          did={getQuery.state?.departmentId}
          viewAs="INSTITUTE"
          isNavbar
        />
      ) : searchParams.get("type") === "cls-detail" ? (
        <InstituteDepartmentClassConfig />
      ) : (
        <InstituteNavbarConfig>
          <InstituteMemberConfig>
            <div className={style.iadc_side}>
              <UserMemberContainerWrapper
                customStyle={{
                  minHeight: "fit-content",
                  paddingBottom: "0",
                }}
              >
                <GoBack
                  customStyle={{
                    width: "100%",
                    marginTop: "0",
                  }}
                />
                <InstituteAcademicDepartmentDetailSidebar />
              </UserMemberContainerWrapper>
            </div>
            <div className={style.iadc_main}>
              <UserMemberContainerWrapper
                customStyle={{
                  paddingBottom: "0",
                }}
              >
                <InstituteAcademicDepartmentDetail
                  instituteId={instituteDepartmentDashboard?.institute}
                  selectBatchId={
                    instituteDepartmentDashboard?.active_academic_batch?._id
                  }
                />
              </UserMemberContainerWrapper>
            </div>
          </InstituteMemberConfig>
        </InstituteNavbarConfig>
      )}
    </>
  );
};

export default InstituteAcademicDepartmentDetailConfig;
