import { useLocation, useSearchParams } from "react-router-dom";
import InstituteDepartmentClassStudent from "./InstituteDepartmentClassStudent/InstituteDepartmentClassStudent";
import InstituteDepartmentPracticalBatchClass from "./InstituteDepartmentPracticalBatchClass/InstituteDepartmentPracticalBatchClass";
import InstituteDepartmentTheoryClass from "./InstituteDepartmentTheoryClass/InstituteDepartmentTheoryClass";
import InstituteDepartmentClassDseStudent from "./InstituteDepartmentClassDseStudent/InstituteDepartmentClassDseStudent";

const InstituteDepartmentClass = () => {
  const getQuery = useLocation();
  const [searchParams] = useSearchParams();
  // console.info("bfvsd ", getQuery.state);
  return (
    <>
      {!searchParams.get("n") ? (
        <InstituteDepartmentClassStudent clsId={getQuery.state?.clsId} />
      ) : searchParams.get("n") === "dse" ? (
        <InstituteDepartmentClassDseStudent clsId={getQuery.state?.clsId} />
      ) : searchParams.get("n") === "theory" ? (
        <InstituteDepartmentTheoryClass
          clsId={getQuery.state?.clsId}
          instituteId={getQuery.state.instituteId}
        />
      ) : searchParams.get("n") === "practical" ? (
        <InstituteDepartmentPracticalBatchClass
          clsId={getQuery.state?.clsId}
          instituteId={getQuery.state.instituteId}
        />
      ) : null}
    </>
  );
};

export default InstituteDepartmentClass;
