import { Suspense } from "react";
import QvipleLoader from "../../../Loader/QvipleLoader";
import { useLocation, useSearchParams } from "react-router-dom";
import InstituteAcademicDepartmentDetailProfile from "./InstituteAcademicDepartmentDetailProfile";
import InstituteAcademicDepartmentStaff from "./InstituteAcademicDepartmentStaff/InstituteAcademicDepartmentStaff";
import InstituteAcademicDepartmentStudent from "./InstituteAcademicDepartmentStudent/InstituteAcademicDepartmentStudent";
import InstituteAcademicDepartmentBatch from "./InstituteAcademicDepartmentBatch/InstituteAcademicDepartmentBatch";
import InstituteAcademicDepartmentMaster from "./InstituteAcademicDepartmentMaster/InstituteAcademicDepartmentMaster";
import InstituteAcademicDepartmentCls from "./InstituteAcademicDepartmentCls/InstituteAcademicDepartmentCls";
const InstituteAcademicDepartmentDetail = ({ instituteId, selectBatchId }) => {
  const [searchParams] = useSearchParams();
  const getQuery = useLocation();
  return (
    <>
      <Suspense fallback={<QvipleLoader />}>
        <InstituteAcademicDepartmentDetailProfile />

        {searchParams.get("type") === "staff" ? (
          <InstituteAcademicDepartmentStaff
            did={getQuery.state?.departmentId}
          />
        ) : searchParams.get("type") === "student" ? (
          <InstituteAcademicDepartmentStudent
            did={getQuery.state?.departmentId}
          />
        ) : searchParams.get("type") === "batch" ? (
          <InstituteAcademicDepartmentBatch
            did={getQuery.state?.departmentId}
            selectBatchId={selectBatchId}
          />
        ) : searchParams.get("type") === "master" ? (
          <InstituteAcademicDepartmentMaster
            did={getQuery.state?.departmentId}
            instituteId={instituteId}
          />
        ) : searchParams.get("type") === "cls" || !searchParams.get("type") ? (
          <InstituteAcademicDepartmentCls
            did={getQuery.state?.departmentId}
            instituteId={instituteId}
          />
        ) : null}
      </Suspense>
    </>
  );
};

export default InstituteAcademicDepartmentDetail;
