import { useState } from "react";
import ModalMenu from "../../../Ui/Modal/ModalMenu";
import { useTranslation } from "react-i18next";
import {
  useInstituteUnPinDepartment,
  useOneDepartmenteDelete,
  useOneDepartmenteEdit,
} from "../../../../Department/Institute/Components/DepartmentApi/depart-api";
import NotifyEvent from "../../../Utils/Snackbar/NotifyEvent";
import PinDepartment from "../../InstituteDepartment/PinDepartment/PinDepartment";
import AddInstituteAcademicDepartmentList from "../InstituteAcademicDepartmentList/AddInstituteAcademicDepartmentList";
import { useNavigate } from "react-router-dom";

const InstituteAcademicDepartmentMenu = ({ onClose, menuData, onRefetch }) => {
  const { t } = useTranslation();
  const [disabled, setDisabled] = useState(false);
  const [step, setStep] = useState("");
  const [snackbar, setSnackbar] = useState({
    open: false,
    msg: "",
    variant: "error",
  });
  const navigate = useNavigate();
  const [departmentDelete] = useOneDepartmenteDelete();
  const [instituteUnPinDepartment] = useInstituteUnPinDepartment();
  const [departmentEdit] = useOneDepartmenteEdit();

  const onStepClose = () => {
    setStep("");
  };

  const onDelete = () => {
    if (menuData?._id) {
      setDisabled((pre) => !pre);
      departmentDelete({
        did: menuData?._id,
      }).then((res) => {
        if (res?.data?.error) {
          setSnackbar({
            msg: res?.data?.message,
            open: true,
            variant: "error",
          });
          setDisabled((pre) => !pre);
        } else {
          setDisabled((pre) => !pre);
          navigate(-1);
          onClose();
        }
      });
    }
  };
  const onUnPinned = () => {
    if (menuData?.institute && menuData?.pin_status_flow) {
      setDisabled((pre) => !pre);
      instituteUnPinDepartment({
        id: menuData?.institute,
        bodyParameter: {
          did:
            menuData?.pin_status_flow === "DEPENDENT"
              ? menuData?.pin_status_id
              : menuData?._id,
          flow: menuData?.pin_status_flow,
        },
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    }
  };

  const onEditAction = (data) => {
    if (menuData?._id) {
      onStepClose();
      setDisabled((pre) => !pre);
      const departmentDetail = {
        dName: menuData?.dName === data.dName ? "" : data.dName,
        dTitle: menuData?.dTitle === data.dTitle ? "" : data.dTitle,
        sid: menuData?.dHead?._id === data.sid ? "" : data.sid,
        gr_initials:
          menuData?.gr_initials === data.gr_initials ? "" : data.gr_initials,
      };
      departmentEdit({
        did: menuData?._id,
        department: departmentDetail,
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    }
  };

  return (
    <>
      {step === 1 ? (
        <PinDepartment
          onClose={() => setStep("")}
          onParentClose={onClose}
          instituteId={menuData?.institute}
          departmentId={menuData?._id}
          onReftech={onRefetch}
        />
      ) : step === 2 ? (
        <AddInstituteAcademicDepartmentList
          isEdit
          onEditAction={onEditAction}
          menuData={menuData}
          onClose={onStepClose}
          instituteId={menuData?.institute}
        />
      ) : (
        <ModalMenu
          onClose={onClose}
          disabled={disabled}
          customStyle={{
            width: "15rem",
          }}
        >
          {menuData?.pin_status === "Pinned" ? (
            <h6 onClick={onUnPinned}>{t("unpin_department")}</h6>
          ) : (
            <h6 onClick={() => setStep(1)}>{t("pin_department")}</h6>
          )}
          <h6 onClick={() => setStep(2)}>{t("edit")}</h6>
          <h6 onClick={onDelete}>{t("delete")}</h6>
        </ModalMenu>
      )}
      <NotifyEvent eventState={snackbar} eventStateHandler={setSnackbar} />
    </>
  );
};

export default InstituteAcademicDepartmentMenu;
